<template lang="html">
    <section class="reply-panel">
        <textarea :id="'answereditor' + flag" :placeholder="placeholder" autofocus></textarea>
        <el-button type="primary" @click.native="reply">{{ btnText }}</el-button>
    </section>
</template>

<script>
import "../assets/plugins/simplemde/simplemde.min.css";
import Simplemde from "../assets/plugins/simplemde/simplemde.min.js";
import tool from "../libs/tool";
import Markdown from "markdown";
import { mapGetters } from "vuex";

let markdown = Markdown.markdown,
    simplemde = null;

export default {
    data() {
        return {
            flag: new Date().getTime(), //
            tagText: '<br/><br/><a class="tag" target="new" href="https://github.com/BubblyPoker/cnode-vue"> cnode-vue</a>',
            placeholder: "...",
            btnText: "",
            replyCache: sessionStorage.replyCache || "" //
        }
    },
    props: ["topic", "replyId", "replyTo"],
    computed: mapGetters({
        user: "getUserInfo"
    }),
    created() {
        if (!this.user.loginname) {
            debugger;
            console.log(tool)
            this.placeholder = "...";
            this.btnText = "";
        }
    },
    watch: {
        "user.loginname"() {
            console.log(this.user.loginname);
            if (!this.user.loginname) {
                this.placeholder = "...";
                this.btnText = "";
            }
            //placeholder
            $(".CodeMirror-placeholder").text(this.placeholder);
        }
    },
    mounted() {
        if (this.replyId) {
            let editor_top = $("#answereditor" + this.flag).offset().top,
                window_top = $(window).scrollTop(),
                window_height = $(window).height();
            if (editor_top > window_top + window_height) {
                $("body").animate({
                    scrollTop: $("#answereditor" + this.flag).offset().top - window_height / 3
                }, 500);
            }
        }
        
        // this.replyTo && simplemde.value("@" + this.replyTo);
        // if (this.topic.id === sessionStorage.topicId) {
        //     this.replyCache && simplemde.value(this.replyCache);
        // }
    },
    methods: {
        unLoginHandle() {
            let replyTmp = document.getElementById("answereditor" + this.flag).value;
            if (replyTmp) {
                sessionStorage.topicId = this.topic.id;
                sessionStorage.replyCache = replyTmp;
            }
            this.$notify.info({
                title: '',
                message: '...'
            });
            this.$router.replace({ name: "login", query: { redirect: encodeURIComponent(this.$route.path) } });
        },
        clearReplyCache() {
            this.replyCache = "";
            sessionStorage.removeItem("topicId");
            sessionStorage.removeItem("replyCache");
        },
        reply() {
            if (!this.user.loginname) {
                this.unLoginHandle();
            } else {
                let self = this,
                    //simplemde.value()
                    //simplemdecodemirror
                    //replyContent
                    replyContent = document.getElementById("answereditor" + this.flag).value || "";
                if (!replyContent) {
                    self.$message({
                        showClose: true,
                        message: "",
                        type: "warning"
                    });
                    return;
                }
                let linkUsers = tool.linkUsers(replyContent),
                    htmlText = markdown.toHTML(linkUsers) + self.tagText,
                    rendered_reply_content = $('<div class="markdown-text"></div>').append(htmlText)[0].outerHTML,
                    post_date = new Date(),
                    post_data = {
                        accesstoken: self.user.accesstoken,
                        content: replyContent + self.tagText,
                    };
                if (self.replyId) {
                    post_data.reply_id = self.replyId;
                }
                $.ajax({
                    url: "https://cnodejs.org/api/v1/topic/" + self.topic.id + "/replies",
                    type: "POST",
                    dataType: "json",
                    data: post_data
                }).done((res) => {
                    if (!res || !res.success) {
                        //TODO   
                        self.$message({
                            showClose: true,
                            message: "",
                            type: "warning"
                        });
                        return;
                    }
                    self.$message({
                        showClose: true,
                        message: "",
                        type: "success"
                    });
                    self.topic.replies.push({
                        id: res.reply_id,
                        author: {
                            loginname: self.user.loginname,
                            avatar_url: self.user.avatar
                        },
                        content: rendered_reply_content,
                        ups: [],
                        create_at: post_date
                    });
                    //
                    simplemde.value("");
                    //comment.vuecommentreply
                    self.$emit("hideReplyPanel");
                    //
                    this.clearReplyCache();
                }).fail((error) => {
                    //TODO   
                    self.$message({
                        showClose: true,
                        message: "",
                        type: "warning"
                    });
                });
            }
        }
    },
    components: {}
}
</script>

<style lang="css"></style>



